.trackList {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 15px;
}

.track {
  display: -ms-flexbox;
  display: flex;
  width: 275px;
  height: 100px;
  margin: 15px;
}

.track-details {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: left;
      align-items: left;
  -ms-flex-pack: center;
      justify-content: center;
  margin-left: 10px;
  width: 100%;
}

a {
  text-decoration: none;
  color: black;
}

a:hover {
  text-decoration: underline;
}

.track-details-title {
  font-size: 14px;
  text-align: left;
  font-weight: bold;
}

.track-details-artist {
  font-size: 14px;
  text-align: left;
}


